* {
  box-sizing: border-box;
  color: #333;
}

body {
  background-color: #81a69b;
  background-image: url("http://pets-images.dev-apis.com/pets/wallpaperB.jpg");
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

#root > div > h1 {
  width: 279px;
  height: 76px;
  text-indent: -9999px;
  filter: brightness(140%);
  background-image: url("http://static.frontendmasters.com/resources/2019-05-02-complete-intro-react-v5/image-logo.png");
  margin: 20px 0;
  display: inline-block;
  overflow: hidden;
}

.search-params {
  width: 1100px;
  margin: 0 auto;
}

.search-params form {
  width: 360px;
  float: left;
  background: #faeff0;
  border-radius: 6px;
  margin: 0 25px 0 0;
  padding: 35px 15px 15px;
  box-shadow: 0 0 12px #aaa, 0 0 12px #fff;
}

.search-params label {
  width: 60px;
  display: block;
}

.search-params input, .search-params select {
  height: 30px;
  width: 325px;
  margin-bottom: 30px;
  font-size: 18px;
}

.search-params button {
  color: #fff;
  cursor: pointer;
  background-color: #ad343e;
  border: 1px solid #333;
  border-radius: 5px;
  margin: 0 auto;
  padding: 5px 25px;
  font-size: 18px;
  display: block;
}

.search-params button:hover {
  background-color: #122622;
}

.search-params button:active {
  background-color: #5f1d22;
}

.search-params button:focus {
  border: 1px solid #6495ed;
}

.search {
  float: left;
  width: 715px;
  background: #faeff0;
  border-radius: 6px;
  margin-bottom: 25px;
  padding: 15px;
  box-shadow: 0 0 12px #aaa, 0 0 12px #fff;
}

.pet {
  width: 100%;
  height: 130px;
  border-bottom: 2px solid #333;
  margin: 25px 0;
  display: block;
  overflow: hidden;
}

.pet img {
  width: 100px;
  min-height: 100px;
}

.image-container {
  clip-path: circle(50%);
  width: 100px;
  height: 100px;
  float: left;
  margin: 0 20px 0 10px;
}

.info {
  float: left;
  width: 80%;
  height: 100px;
  flex-direction: column;
  justify-content: space-around;
  padding-top: 10px;
}

.pet h1 {
  white-space: nowrap;
  color: #333;
  width: 95%;
  text-overflow: ellipsis;
  margin: 0;
  font-size: 30px;
  font-weight: normal;
  overflow: hidden;
}

.pet h2 {
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0;
  font-size: 20px;
  font-weight: normal;
}

